@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
    @apply px-5;
}

.gray-link {
    color: #333333;
}

.bg-primary{
    background: linear-gradient(120deg, rgb(240, 147, 251) 0%, rgb(245, 87, 108) 100%)
}

.bg-secondary{
    background: linear-gradient(120deg, rgb(200, 107, 211) 0%, rgb(205, 47, 68) 100%);
}

.font-primary {
    font-size: 20px;
    font-family: 'Poppins', sans-serif;
}

.font-secondary{
    font-family: 'Poppins', sans-serif;
}

.mobile-nav{
    @apply fixed top-10 left-0 w-full h-1/4;
    background-color: #D9D9D9
}

.mobile-nav li {
    @apply w-full h-full text-center border-b-2;
}

.pro-style{
    @apply absolute left-0 right-0 bottom-0 top-0 bg-secondary opacity-0 duration-500 hover:opacity-100
}

.btn {
    @apply bg-secondary px-5 py-2 font-bold hover:border-2 border-black rounded;
}

html {
    scroll-behavior: smooth;
}

  